import React from 'react'
import { Evaluator as EvaluatorComponent } from '@aserto/console-common'

import { useActiveConfigurationName } from '../../../../services/ActiveConfiguration'
import Frame from '../../Frame'

const Evaluator: React.FC = () => {
  const { configurationName } = useActiveConfigurationName()

  return (
    <Frame>
      <EvaluatorComponent authorizerPolicyInstance={{ authorizerInstance: configurationName }} />
    </Frame>
  )
}

export default Evaluator
